const timerBtn = document.querySelector("#timer");
const timerContainer = document.querySelector("#timer-container");

const addTimerFormBtn = document.getElementById("add-timer-form-btn");
const stopTimerBtn = document.getElementById("stop-timer-btn");

const timerForm = document.getElementById("timer-form"); //타이머폼
const addTimerBtn = document.getElementById("add-timer-btn"); //타이머등록버튼

const timerUserFormBtn = document.getElementById("timer-user-form-btn"); //사용자설정버튼
const timerUserForm = document.getElementById("timer-user-form"); //사용자설정 폼
const timerDefaultForm = timerForm.querySelector("#timer-default-form");

const timerHours = document.getElementById("timer-hours");
const timerMinutes = document.getElementById("timer-minutes");

// 타이머 컨테이너를 엽니다.
function openTimerContainer(event) {
  timerContainer.classList.toggle("hidden");
}

// 타이머 등록 폼 열기
function openAddTimerForm() {
  timerForm.classList.toggle("hidden");
}

// 타이머 사용자폼 열기
function openTimerUserForm() {
  timerUserForm.classList.toggle("hidden");
}

// 타이머 등록
function addTimer() {}

timerBtn.addEventListener("click", openTimerContainer); // 타이머컨테이너 열기
addTimerFormBtn.addEventListener("click", openAddTimerForm); //타이머등록폼 열기
timerUserFormBtn.addEventListener("click", openTimerUserForm); //타이머 사용자폼 열기
addTimerBtn.addEventListener("click", addTimer); //타이머 등록하기
